@import url("https://fonts.googleapis.com/css?family=Jost");

.balance {
  font-family: "Jost";
  width: 510px;
  height: 150px;

  background: #eeeeee;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 39px;
  position: relative;
  z-index: 1;
}

.balance-wallet-button {
  position: absolute;
  display: flex;
  top: 30%;
  left: 30%;
  z-index: 2;
}

.balance-wallet-button button {
  background-color: rgba(0, 128, 105, 0.9);
  box-shadow: none;
}

.balance-wallet-button button:hover {
  box-shadow: none;
}

.user-name {
  height: 30%;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 0 5%;
  justify-content: center;
  gap: 5%;
}

.user-icon {
  width: 35px;
  height: 35px;
}

.user-name-text {
  font-size: 30px;
  font-weight: 400;
  opacity: 0.5;
  margin: 0;
}

.bold {
  font-weight: 600;
  opacity: 1;
}

.token-balance {
  height: 90px;
  padding: 10px 15% 10px 10%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.token-balance img {
  height: 60px;
}

.icon-amount {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 20px;
}

.loader {
  height: 50%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
