@import url("https://fonts.googleapis.com/css?family=Jost");

.rules-modal {
  font-family: "Jost";
  position: absolute;
  width: 700px;
  height: 400px;
  left: 25%;
  top: 25%;
  background-color: #fff;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 40px;
  z-index: 2;
  padding: 20px;
  opacity: 0;
  animation: opacity 0.1s forwards;
}

@keyframes opacity {
  100% {
    opacity: 1;
  }
}

.cancel-image img {
  width: 20px;
  height: 20px;
  cursor: pointer;
}

.cancel-image {
  display: flex;
  justify-content: flex-end;
}

.modal-column {
  display: flex;
  flex-direction: column;
}

.eaqual-sign {
  margin-left: 40px;
}

.plus-sign {
  margin-left: 10px;
}

.modal {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.modal h4 {
  padding: 0 5%;
  font-size: 24px;
  font-weight: 400;
}

.modal img {
  height: 40px;
  width: 40px;
  padding: 0 5%;
}
